"use client"

import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import { supabase } from "@/lib/supabase"
import { Button } from "@/components/ui/button"
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"

type Course = {
  id: string
  title: string
  description: string | null
  icon_name: string | null
  total_lessons: number
  created_at: string
}

const ICON_OPTIONS = [
  "book-open",
  "shopping-bag",
  "utensils-crossed",
  "plane",
  "briefcase",
  "languages",
]

export default function AdminCoursesPage() {
  const router = useRouter()
  const [courses, setCourses] = useState<Course[]>([])
  const [loading, setLoading] = useState(true)
  const [dialogOpen, setDialogOpen] = useState(false)
  const [title, setTitle] = useState("")
  const [description, setDescription] = useState("")
  const [iconName, setIconName] = useState("book-open")
  const [saving, setSaving] = useState(false)

  useEffect(() => {
    const loadCourses = async () => {
      try {
        const { data, error } = await supabase
          .from("courses")
          .select("*")
          .order("created_at", { ascending: false })

        if (error) {
          console.error("Failed to load courses:", error)
        } else if (data) {
          setCourses(data as Course[])
        }
      } finally {
        setLoading(false)
      }
    }

    loadCourses()
  }, [])

  const refreshCourses = async () => {
    const { data, error } = await supabase
      .from("courses")
      .select("*")
      .order("created_at", { ascending: false })
    if (!error && data) {
      setCourses(data as Course[])
    }
  }

  const handleCreateCourse = async () => {
    if (!title.trim()) return
    setSaving(true)
    try {
      const { error } = await supabase.from("courses").insert({
        title: title.trim(),
        description: description.trim() || null,
        icon_name: iconName || null,
      })
      if (error) {
        console.error("Failed to create course:", error)
        alert("创建课程失败，请检查控制台日志。")
        return
      }
      setDialogOpen(false)
      setTitle("")
      setDescription("")
      setIconName("book-open")
      await refreshCourses()
    } finally {
      setSaving(false)
    }
  }

  const handleDeleteCourse = async (id: string) => {
    if (!confirm("确定要删除这个课程吗？此操作不可恢复。")) return
    const { error } = await supabase.from("courses").delete().eq("id", id)
    if (error) {
      console.error("Failed to delete course:", error)
      alert("删除失败，请检查控制台日志。")
      return
    }
    await refreshCourses()
  }

  return (
    <div className="min-h-screen bg-gradient-to-b from-slate-50 via-white to-slate-100 px-4 py-10">
      <div className="max-w-5xl mx-auto space-y-8">
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-3xl font-bold text-slate-900">课程管理</h1>
            <p className="text-slate-600 mt-1 text-sm">
              管理首页展示的课程（仅 Admin 可编辑，RLS 已限制）
            </p>
          </div>
          <Dialog open={dialogOpen} onOpenChange={setDialogOpen}>
            <DialogTrigger asChild>
              <Button className="rounded-xl bg-teal-600 hover:bg-teal-700">
                新建课程
              </Button>
            </DialogTrigger>
            <DialogContent className="max-w-md rounded-2xl">
              <DialogHeader>
                <DialogTitle>新建课程</DialogTitle>
              </DialogHeader>
              <div className="space-y-4 mt-2">
                <div className="space-y-2">
                  <Label htmlFor="title">标题</Label>
                  <Input
                    id="title"
                    value={title}
                    onChange={(e) => setTitle(e.target.value)}
                    placeholder="如：购物场景"
                  />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="description">描述</Label>
                  <Input
                    id="description"
                    value={description}
                    onChange={(e) => setDescription(e.target.value)}
                    placeholder="简要说明这个课程的内容"
                  />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="icon">图标名称（Lucide 名称）</Label>
                  <Input
                    id="icon"
                    list="icon-options"
                    value={iconName}
                    onChange={(e) => setIconName(e.target.value)}
                    placeholder="如：shopping-bag"
                  />
                  <datalist id="icon-options">
                    {ICON_OPTIONS.map((name) => (
                      <option key={name} value={name} />
                    ))}
                  </datalist>
                  <p className="text-xs text-slate-500">
                    将在前端通过 icon_name 匹配 Lucide 图标。
                  </p>
                </div>
                <div className="flex justify-end gap-2 pt-2">
                  <Button
                    variant="outline"
                    className="rounded-xl"
                    onClick={() => setDialogOpen(false)}
                  >
                    取消
                  </Button>
                  <Button
                    className="rounded-xl bg-teal-600 hover:bg-teal-700"
                    onClick={handleCreateCourse}
                    disabled={saving || !title.trim()}
                  >
                    {saving ? "保存中..." : "保存课程"}
                  </Button>
                </div>
              </div>
            </DialogContent>
          </Dialog>
        </div>

        <div className="bg-white rounded-2xl shadow-sm border border-slate-100 p-4">
          {loading ? (
            <div className="py-10 text-center text-slate-500 text-sm">加载中...</div>
          ) : courses.length === 0 ? (
            <div className="py-10 text-center text-slate-500 text-sm">
              还没有任何课程，点击右上角“新建课程”开始添加。
            </div>
          ) : (
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>标题</TableHead>
                  <TableHead>描述</TableHead>
                  <TableHead>图标</TableHead>
                  <TableHead className="w-24 text-right">操作</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {courses.map((course) => (
                  <TableRow
                    key={course.id}
                    className="cursor-pointer hover:bg-slate-50"
                    onClick={() => router.push(`/admin/courses/${course.id}`)}
                  >
                    <TableCell className="font-medium">{course.title}</TableCell>
                    <TableCell className="text-slate-600">
                      {course.description || "—"}
                    </TableCell>
                    <TableCell className="text-xs text-slate-500">
                      {course.icon_name || "—"}
                    </TableCell>
                    <TableCell
                      className="text-right"
                      onClick={(e) => {
                        e.stopPropagation()
                      }}
                    >
                      <Button
                        variant="outline"
                        className="text-red-600 border-red-100 hover:bg-red-50 rounded-xl"
                        size="sm"
                        onClick={() => handleDeleteCourse(course.id)}
                      >
                        删除
                      </Button>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          )}
        </div>
      </div>
    </div>
  )
}


